import React from 'react';

class TemplateCSV extends React.Component {
render() {
    var path = `${process.env.PUBLIC_URL}templates/drivers.csv`;
    console.log(path);
    return (
      <div>

        <a href={path} download="template.csv">Download</a>
      </div>
    );
  }
}

export default TemplateCSV



